*{
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	border: 0;
}
a, a:visited{
	text-decoration: none;
	color: #ffffff;
}
a:hover{
	text-decoration: none;
}
a:active{
	text-decoration: none;
	color: #ffffff;
}
a:link{
	text-decoration: none;
	color: #ffffff;
}
ul li{
	text-decoration: none;
	list-style: none;
}
img{
	vertical-align: top;
}
h1,h2,h3,h4,h5,h6{
	font-size: inherit;
	font-weight: 400;
}
nav{
	display: block;
}
html,body{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	background: #fff;
	font-family: 'Roboto', sans-serif;
}

// Colors | Fonts | Font-size

$color-btn: #00e0d0;
$color-light: #ffffff;
$color-dark: #413d4b;

$title-font: 'Playfair Display', serif;
$text-font: 'Roboto', sans-serif;

$title-fz: 22px;
$text-fz: 14px;

// Mixins

@mixin titleStyle{
	font-family: $title-font;
	font-size: $title-fz;
	font-weight: 700;
}

/* Standard for layout */

section{
	padding: 70px 0;
}

.button{
	font-size: 16px;
	font-weight: 700;
	color: $color-btn;
	padding: 15px;
	border: 2px solid $color-btn;
	border-radius: 4px;
	background: transparent;
	cursor: pointer;
	transition: 0.7s;
	&:hover{
		border: 2px solid $color-dark;
		background: $color-btn;
		color: $color-dark;
		transition: 0.7s;
	}
}


/* Main Styles */

header{
	position: relative;
	background: url("../img/header/header-bg.jpg") center no-repeat;
	background-size: cover;
	z-index: 1;
	padding-top: 30px;
}
header:before{
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(90deg, #0f0b2f, #301f57);
	opacity: 0.8;
	z-index: -1;
}
.header{
	position: relative;
}

.header-logo{
	display: flex;
	align-items: center;
	img{
		margin-right: 10px;
	}
	p{
		font-size: 30px;
		font-weight: 300;
		color: $color-btn;
		background: linear-gradient(90deg, #02ded0, #4794bc);
		background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}

.header-nav{
	position: absolute;
	right: 0;
}

.header-row{
	align-items: center;
}

.menu{
	position: relative;
	right: 0;
	&__list{
		position: absolute;
		right: 0;
		display: flex;
		margin-top: -5px;
		>li{
			position: relative;
			margin-right: 20px;
			&:last-child{
				margin-right: 0;
			}
		}
	}
	&__link{
		font-size: 13px;
		font-weight: 700;
		color: $color-light;
		transition: 0.4s;
		padding: 15px 0;
		&:hover{
			color: $color-btn;
			transition: 0.4s;
		}
	}
}

.menu__list>li:hover .sub-menu__list{
	display: inline-block;
}

.arrow{
	position: absolute;
	top: 3px;
	margin-left: 3px;
	border-top: 5px solid $color-light;
	border-right: 5px solid transparent;
	border-left: 5px solid transparent;
	transition: 0.4s;
	&:hover{
		border-top: 6px solid $color-btn;
		transition: 0.4s;
	}
}
.menu__link:hover .arrow{
	border-top: 6px solid $color-btn;
	transition: 0.4s;
}
	
.sub-menu{
	&__list{
		display: none;
		position: absolute;
		left: 0;
		top: 0;
		margin-top: 25px;
		>li{
			border: 1px solid $color-btn;
			padding: 10px 15px 10px 5px;
		}
	}
	&__link{
		font-size: 13px;
		font-weight: 700;
		color: $color-light;
		padding: 5px;
		transition: 0.4s;
		&:hover{
			color: $color-btn;
			transition: 0.4s;
		}
	}
}


.header-descr{
	width: 80%;
	text-align: center;
	padding-top: 165px;
	margin: auto;
	position: relative;
	&__title{
		font-family: $title-font;
		font-size: 32px;
		font-weight: 700;
		color: $color-light;
		&:after{
		    content: "";
		    display: block;
		    margin: 0 auto;
		    width: 50px;
		    margin-top: 25px;
		    border-bottom: 2px solid $color-btn;
		}
	}
	&__subtitle{
		font-size: $text-fz;
		color: $color-light;
		line-height: 24px;
		margin-top: 30px;
	}
	&__button{
		margin-top: 50px;
	}
}

.prev, .next{
	cursor: pointer;
	position: absolute;
	top: 60%;
	width: auto;
	color: $color-btn;
	font-size: 18px;
	border: 2px solid $color-btn;
	border-radius: 50%;
	padding: 10px 15px;
	transition: 0.6s;
}
.prev{
	margin-left: -70px;
}
.next{
	right: 0;
	margin-right: -70px;
}
.prev:hover, .next:hover{
	color: $color-dark;
	border: 2px solid $color-dark;
	background: $color-btn;
}

.dots{
	text-align: center;
	padding: 120px 0 70px 0;
}
.dot{
	cursor: pointer;
	height: 12px;
	width: 12px;
	margin: 0 3px;
	background-color: transparent;
	border: 1px solid $color-light;
	border-radius: 50%;
	display: inline-block;
	transition: 0.4s;
	&:hover{
		background-color: $color-btn;
		border: 1px solid $color-btn;
	}
}
.active{
	background-color: $color-btn;
	border: 1px solid $color-btn;
}
.fade{
	animation-name: fade;
	animation-duration: 1.5s;
}
@keyframes fade{
	from {opacity: 0.4}
	to {opacity: 1}
}



.about{
	&__logo{
		img{
			max-width: 100%;
		}
	}
	&__descr{
		h4{
			font-family: $title-font;
			font-size: $title-fz;
			font-weight: 700;
			color: $color-dark;
		}
		p{
			font-size: $text-fz;
			color: $color-dark;
			margin-top: 30px;
			line-height: 22px;
		}
	}
	&__button{
		margin-top: 30px;
	}
}



.expertise{
	padding: 30px 0 100px 0;
	&__title{
		text-align: center;
		&:after{
			content: "";
			display: block;
			width: 50px;
			margin: 0 auto;
			margin-top: 20px;
			border-bottom: 2px solid #c6c4c9;

		}
		h4{
			@include titleStyle;
			color: $color-dark;
		}
		p{
			font-size: $text-fz;
			color: $color-dark;
			margin-top: 15px;
		}
	}
	&__blocks{
		padding: 80px 0;
		&:last-child{
			padding: 0;
		}
	}
	&__item{
		text-align: center;
		padding: 0 30px;
		h5{
			font-size: $text-fz;
			font-weight: 700;
			color: $color-dark;
			margin-top: 35px;
		}
		p{
			font-size: $text-fz;
			color: #333333;
			line-height: 22px;
			margin-top: 25px;
		}
	}
}



.team{
	position: relative;
	background: url("../img/team/team-bg.jpg") center no-repeat;
	background-size: cover;
	z-index: 1;
	&:before{
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(90deg, #0f0b2f, #301f57);
		opacity: 0.8;
		z-index: -1;
	}
	&__title{
		text-align: center;
		&:after{
			content: "";
			display: block;
			margin: 0 auto;
			width: 50px;
			margin-top: 15px;
			border-bottom: 2px solid #655c80;
		}
		h4{
			@include titleStyle;
			color: $color-light;
		}
		p{
			font-size: 14px;
			color: $color-light;
			margin-top: 15px;
		}
	}
	&__people{
		margin-top: 60px;
		text-align: center;
	}
	&__img{
		padding: 0 0 90% 0;
		overflow: hidden;
		position: relative;
		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	&__descr{
		border: 1px solid #7c7890;
		border-top: none;
		h5{
			font-size: $text-fz;
			font-weight: 700;
			color: $color-light;
			padding-top: 20px;
		}
		p{
			font-size: 13px;
			color: $color-light;
			margin-top: 10px;
			margin-bottom: 25px;
		}
	}
	&__bot{
		text-align: center;
		margin-top: 70px;
		p{
			font-size: 14px;
			color: $color-light;
		}
		button{
			margin-top: 25px;
		}
	}
}



.works{
	&__title{
		h4{
			@include titleStyle;
			color: $color-dark;
		}
	}
	&__link{
		a{
			font-size: $text-fz;
			color: $color-dark;
			cursor: pointer;
			&:hover{
				border-bottom: 1px dashed $color-dark;
			}
		}
	}
	&__block{
		margin: 70px 0 0 0;
	}
	&__item{
		padding: 0 0 15% 0;
		overflow: hidden;
		position: relative;
		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
			position: absolute;
			top: 0;
			left: 0;
		}
		&:hover .works__content{
			display: inline-block;
			animation-name: works;
			animation-duration: 0.6s;
		}
	}
	&__content{
		text-align: center;
		position: absolute;
		display: none;
		background-color: #1b173a;
		width: 100%;
		height: 100%;
		opacity: 0.8;
		h5{
			font-size: $text-fz;
			font-weight: 700;
			color: $color-light;
			padding-top: 60px;
		}
		p{
			font-size: 13px;
			color: $color-light;
			margin: 10px 20px;
			line-height: 22px;
		}
	}
	&__button{
		text-align: center;
		margin-top: 70px;
	}
}
@keyframes works{
	from {opacity: 0;}
	to {opacity: 0.8;}
}



.map{
	position: relative;
	background: url("../img/map/map-bg.jpg") center no-repeat;
	background-size: cover;
	z-index: 1;
	&:before{
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(90deg, #0f0b2f, #301f57);
		opacity: 0.8;
		z-index: -1;
	}
	&__descr{
		h4{
			@include titleStyle;
			color: $color-light;
		}
		p{
			font-size: $text-fz;
			color: $color-light;
			line-height: 22px;
			margin-top: 30px;
		}
	}
	&__yandex{

	}
}



.contact{
	&__form{
		&-item{
			font-family: $text-font;
			font-size: $text-fz;
			width: 100%;
			border: 1px solid #e5e5e5;
			border-radius: 2px;
			padding: 15px 0 15px 20px;
			margin-top: 25px;
			&:first-child{
				margin-top: 40px;
			}
			&:focus{
				color: $color-dark;
				outline: none;
				border: 1px solid $color-dark;
			}
		}
		textarea{
			min-height: 200px;
			resize: none;
		}
	}
	&__button{
		margin-top: 25px;
	}
	&__title{
		h4{
			@include titleStyle;
			color: $color-dark;
		}
	}
	&__clients{
		text-align: center;
		img{
			margin-top: 50px;
		}
	}
}



.footer{
	position: relative;
	background: url("../img/footer/footer-bg.jpg") center no-repeat;
	background-size: cover;
	z-index: 1;
	padding: 70px 0;
	&:before{
		content: "";
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(90deg, #0f0b2f, #301f57);
		opacity: 0.8;
		z-index: -1;
	}
	&__block{
		margin-top: 20px;
		&:first-child{
			margin-top: 0;
		}
		h5{
			font-size: 15px;
			color: $color-light;
			font-weight: 700;
		}
		p:not(.footer__grad){
			font-size: $text-fz;
			color: $color-light;
			line-height: 26px;
		}
	}
	&__logo{
		display: flex;
		align-items: center;
		margin-bottom: 30px;
		img{
			margin-right: 10px;
		}
		p{
			font-size: 30px;
			font-weight: 300;
			color: $color-btn;
			background: linear-gradient(90deg, #02ded0, #4794bc);
			background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
	&__address{
		margin-top: 46px;
		img{
			margin-right: 10px;
		}
	}
	&__phone{
		margin-top: 10px;
		img{
			margin-right: 10px;
		}
	}
	&__form{
		margin-top: 35px;
		input{
			border: 1px solid #9f96b0;
			background: transparent;
			padding: 15px;
			margin-right: 10px;
			margin-top: 10px;
			&::placeholder{
				color: #fff;
			}
			&:focus{
				color: #fff;
			}
		}
		button{
			cursor: pointer;
			padding: 28px 35px 19px 30px;
			background: #9f96b0;
			position: relative;
			img{
				position: absolute;
				left: 30%;
				top: 25%;
			}
		}
	}
	&__social{
		margin-top: 20px;
		&-item{
			position: relative;
			display: inline-block;
			margin-right: 10px;
			border: 1px solid $color-light;
			border-radius: 50%;
			min-width: 35px;
			min-height: 35px;
			transition: 0.6s;
			text-align: center;
			&:hover{
				border: 1px solid $color-btn;
				transition: 0.6s;
			}
			img{
				padding-top: 9px;
			}
		}
	}
	&__thanks{
		margin-top: 30px;
		border-top: 1px solid #fff;
		padding-top: 15px;
		p{
			font-size: 18px;
			color: $color-light;
			line-height: 26px;
		}
		span{
			font-weight: 700;
		}
		a{
			transition: 0.6s;
			&:hover{
				color: $color-btn;
				transition: 0.6s;
			}
		}
	}
}